.section {
  &__title {
    font-family: 'Film P3', sans-serif;
    font-weight: 700;
    font-size: 32px;
    line-height: 1.075;
    letter-spacing: 0.3em;
    text-transform: uppercase;
    color: var(--titles-of-sections);
    @include min_414 {
      font-size: 40px;
    }
    @include min_1024 {
      font-size: 64px;
    }
    &--light {
      color: var(--text-on-dark-background-and-white-elements);
    }
    & > span {
      letter-spacing: normal;
    }
  }
  &__subtitle {
    color: var(--subtitles-font-color);
    font-family: 'Times New Roman', sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.15;
    @include min_1024 {
      font-size: 32px;
    }
  }
  &__text {
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    letter-spacing: 0.02em;
    @media screen and (min-width: 1024px) {
      font-size: 16px;
    }
    &--light {
      color: var(--grey-text-on-dark-background);
      text-align: left;
    }
    &--darkyellow {
      color: var(--brown-yellow-text-color);
    }
  }
  &__department {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: 0.02em;
    color: var(--text-on-dark-background-and-white-elements);
    margin-top: 12px;
    margin-bottom: 20px;
    @include min_1024 {
      margin-top: 16px;
      margin-bottom: 8px;
      font-size: 24px;
    }
  }
}

.room__company-title {
  margin-top: 8px;
  margin-bottom: 32px;
  @include min_1024 {
    margin-top: 4px;
    margin-bottom: 62px;
  }
  @include min_1280 {
    margin-bottom: 72px;
  }
}

.room__text {
  max-width: 258px;
  @include min_1024 {
    max-width: 439px;
  }
  @include min_1280 {
    max-width: 400px;
  }
}

.modal {
  &__title {
    font-family: 'Times New Roman', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.16;
    letter-spacing: 0.05em;
    color: var(--dark-color-for-elemenents-and-text);
    text-align: center;
    margin-bottom: 40px;
    text-transform: uppercase;
    @include min_1024 {
      font-size: 40px;
    }
  }
  &__subtitle {
    font-family: 'Times New Roman', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.16;
    letter-spacing: 0.05em;
    color: var(--forms-placeholder-font-color);
    margin-bottom: 28px;
  }
  &__label {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.2;
    letter-spacing: 0.02em;
    color: var(--brown-yellow-text-color);
    margin-top: 8px;
    margin-left: 12px;
    margin-bottom: 20px;
    @include min_1024 {
      margin-bottom: 28px;
    }
    &:last-of-type {
      margin-bottom: 0;
    }
    @include min_1024 {
      font-size: 16px;
    }
  }
}

.room__title {
  & > span {
    text-transform: none;
  }
  text-transform: uppercase;
  font-family: 'Times New Roman';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.16;
  letter-spacing: 0.05em;
  color: var(--dark-color-for-elemenents-and-text);
  margin-bottom: 12px;
  @include min_1024 {
    font-size: 30px;
    margin-bottom: 16px;
  }
  @include min_1280 {
    font-size: 36px;
    line-height: 1.14;
    margin-bottom: 24px;
  }
}
